import api from '@/common/index.js';
import uniNumberBox from '@/components/uni-number-box.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import scrllTop from '@/components/uni-scrollTo/uni-scrollTo.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
let eindex = {
	statusHeight: 0,
	historyList: [],
	isHistory: true,
	text: "",
	thispage: 0,
	Allpage: 0,
	all: 0,
	sort: false,
	recList_a: [],
	recList_b: [],
	page: 1,
	loadingType: 'more', //加载更多状态
	type: 'add',
	filterIndex: 0,
	priceOrder: 0, //1 价格从低到高 2价格从高到低
	downNum: '',
	getTop: 0,
	getTop2: 0,
	appwidth: '100%',
	scrllHdid: false,
	over: false,
	satus_a: false,
	satus_b: false,
};
export default {
	components: {
		uniNumberBox,
		uniLoadMore,
		scrllTop,
		imglazy
	},

	data() {
		return {
			imglazyStyle: 'border-radius: 10upx;',
			list: {
				statusHeight: 0,
				historyList: [],
				isHistory: true,
				text: "",
				thispage: 0,
				Allpage: 0,
				all: 0,
				sort: false,
				recList_a: [],
				recList_b: [],
				page: 1,
				loadingType: 'more', //加载更多状态
				type: 'add',
				filterIndex: 0,
				priceOrder: 0, //1 价格从低到高 2价格从高到低
				downNum: '',
				getTop: 0,
				getTop2: 0,
				appwidth: '100%',
				scrllHdid: false,
				over: false,
				satus_a: false,
				satus_b: false,
			}
		};
	},
	onLoad() {
		let that = this;
		setTimeout(function() {
			eindex = that.list;
			that.getTitleHeight();
			that.seachData();
		}, 300)
	},
	computed: {
		i18n() {
			return this.$t('search')
		}
	},
	methods: {
		// 判断国外关键字跳转
		bindnav(e) {
			switch (e) {
				case 0:
					uni.navigateTo({
						url: `/pages/product/list?listId=new`
					})
					break;
				case 1:
					uni.navigateTo({
						url: `/pages/product/list?listId=China`
					})
			}
		},
		// 判断国内外关键字推荐
		seachData() {
			eindex.satus_a = true;
			eindex.recList_a = this.$t('search').recList_a;
			this.list = eindex;
			// 				const data = uni.getStorageSync('userinfo');
			// 				if (data.oversea == 1) {
			// 					console.log("1")
			// 					this.recList_b = this.$t('search').recList_b
			// 					this.satus_b = true;
			// 					this.satus_a = false;
			// 
			// 				} else {
			// 					console.log("2")
			// 					this.recList_a = this.$t('search').recList_a
			// 					this.satus_b = false;
			// 					this.satus_a = true;
			// 				}
		},
		getTitleHeight() {
			let that = this;
			uni.getSystemInfo({
				success: res => {
					console.log(res.screenHeight)
					if (res.screenHeight < 740) {

						eindex.statusHeight = "90px";
						eindex.getTop = '32px';
						if (eindex.sort == true) {
							eindex.getTop2 = '120px';
						} else {
							eindex.getTop2 = '70px';
						}
						that.list = eindex
						return false;
					}
					if (res.screenHeight < 950) {

						eindex.statusHeight = "95px";
						eindex.getTop = '40px';

						if (eindex.sort == true) {
							eindex.getTop2 = '130px';
						} else {
							eindex.getTop2 = '80px';
						}
						that.list = eindex
						return false;
					}
					if (res.screenHeight > 1000) {

						eindex.statusHeight = "140px";
						eindex.getTop = '30px';
						if (eindex.sort == true) {
							eindex.getTop2 = '220px';
						} else {
							eindex.getTop2 = '160px';
						}
						that.list = eindex
						return false;
					}
				}
			})
		},
		
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			eindex[key] = e.detail.value;
		},
		postData() {
			var that = this;
			if (eindex.text == "") {
				eindex.isHistory = true;
				eindex.over = true;
				that.list = eindex;
				this.$api.msg(that.$t('search').pl)
				return false;
			}
			eindex.page = 1;
			eindex.over = false;
			eindex.historyList = [];
			api.load(this.$t('index').loadmsg)
			this.loadData()

		},
		bindSea(option) {

			eindex.text = option;
			api.load(this.$t('index').loadmsg)
			this.loadData();
		},
		//下拉刷新
		onPullDownRefresh() {
			if (eindex.text != '') {
				eindex.page = 1
				eindex.type = 'refresh';
				this.loadData();
			} else {
				uni.stopPullDownRefresh();
			}
		},
		//加载更多
		onReachBottom() {
			eindex.type = 'add';
			this.loadData();
		},
		// 键盘搜索
		bindconfirm(){
			this.postData();
		},
		loadData(data) {
			var that = this;
			eindex.over = false;

			let items = {
				text: eindex.text,
				page: eindex.page,
				downNum: eindex.downNum
			}
			that.$ajax.searchData(items).then(res => {
				console.log(res)
				let content = res.list;
				// 当数据为空时
				if (res.list == null || res.other.totalPage == 0) {
					eindex.loadingType = "nomore";
					eindex.isHistory = false;
					eindex.over = true;
					
					eindex.thispage=0;
					eindex.Allpage=0;
					that.list = eindex
					uni.hideLoading();
					return false;
				};
				if (res.list == 0) {
					if (res.other.totalPage == 0) {
						eindex.loadingType = "nomore";
						eindex.historyList = [];
						eindex.isHistory = false;
						that.over = true;
					}
				};
				let list = [];
				eindex.Allpage = res.other.totalPage;
				eindex.thispage = res.other.thispage;
				eindex.all = res.other.all;
				eindex.sort = true;
				eindex.scrllHdid = true;
				that.getTitleHeight();
				for (let i in content) {
					content[i].image = that.$store.getters.imgShow + '/thumb/' + content[i].item_no +
						'.jpg?x-oss-process=style/204';
					content[i].allNum = parseInt(content[i].spec);
					content[i].mini=parseInt(content[i].spec);
					content[i].circleHidden = false;
					content[i].circleText = 0;
					if (content[i].cnum != "0") {
						content[i].circleHidden = true;
					}
					list.push(content[i]); //属性
				}
				eindex.isHistory = false;

				eindex.loadingType = parseInt(res.other.thispage) > res.other.totalPage ? 'nomore' : 'more';
				if (eindex.type == 'add') {
					eindex.page = eindex.page + 1;

					if (eindex.loadingType === 'nomore') {
						that.list = eindex
						return;
					}
					eindex.loadingType = 'loading';
				} else {
					eindex.loadingType = 'more'
				};
				if (eindex.type == 'refresh') {
					eindex.historyList = [];
					uni.stopPullDownRefresh();
					eindex.page = eindex.page + 1;

					if (eindex.loadingType === 'nomore') {
						that.list = eindex;
						return;
					}
					eindex.loadingType = 'loading';
				}
				// 当前页面小于20条
				if (list.length < 20) {
					eindex.loadingType = 'nomore';
					uni.hideLoading();
					eindex.historyList = eindex.historyList.concat(list);
					return false;
				}
				uni.hideLoading();
				eindex.historyList = eindex.historyList.concat(list);

				that.list = eindex;
			})
		},
		//总数量
		bindChange(data) {
			eindex.historyList[data.index].allNum = data.number;
			this.list = eindex;
		},
		//添加购物车
		shopping(data) {
			let that = this;
			let cartInfo = {
				item_no: data.item_no,
				allNum: data.allNum
			};
			that.$ajax.addShop(cartInfo).then(res => {
				if (res.result == "success") {
					that.$api.msg(that.$t('index').msg1);
					data.circleHidden = true;
					data.cnum = parseInt(data.cnum) + parseInt(data.allNum)
				} else {
					that.$api.msg(res.msg);
				}
			})
		},
		goback() {
			uni.navigateBack({
				delta: 1
			})
		},
		//详情页
		navToDetailPage(item) {

			uni.navigateTo({
				url: `/pages/product/product?productId=${item}`
			})
		},
		// 扫描商品
		onScan() {
			uni.scanCode({
				success: function(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					uni.navigateTo({
						url: `/pages/product/product?productId=${res.result}`
					})
				}
			});
		},
		//筛选点击
		tabClick(index) {
			eindex.filterIndex = index;
			eindex.page = 1;
			eindex.type = 'refresh';
			if (index === 2) {

				eindex.priceOrder = eindex.priceOrder === 1 ? 2 : 1;
				if (eindex.priceOrder == 1) {
					eindex.downNum = "desc"
				};
				if (eindex.priceOrder == 2) {
					eindex.downNum = "asc"
				};

				this.loadData();
			} else {

				eindex.priceOrder = 0;
				eindex.downNum = ''
				this.loadData();
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})

			api.load(this.$t('index').loadmsg)

		},
	},

	onNavigationBarSearchInputChanged(e) {
		eindex.text = e.text;
	},
};
